/* global moment:false */
"use strict";

/*
 * Countdown timer to specific date and time
 */
(function () {

	var timeElement, countdownElement, eventTime, currentTime, duration, interval, intervalId;

	interval = 1000; // 1 second

	// calculate difference between two times
	eventTime = moment.tz("2022-06-07T10:00:00", "America/Los_Angeles");
	// based on time set in user's computer time / OS
	currentTime = moment.tz();
	// get duration between two times
	duration = moment.duration(eventTime.diff(currentTime));

	// loop to countdown every 1 second
	setInterval(function(){
		// get updated duration
		duration = moment.duration(duration - interval, 'milliseconds');

		// if duration is >= 0
		if (duration.asSeconds() <= 0) {
			clearInterval(intervalId);
			// hide the countdown element
			timeElement = document.querySelector("time");
			timeElement.classList.add("hidden");
		} else {
			// otherwise, show the updated countdown
			countdownElement = document.querySelector("time .countdown");
			countdownElement.innerHTML = "<div><span class='num'>"+ duration.days() + "</span><span class='label'>Days</span></div><div><span class='num'>" + duration.hours() + "</span><span class='label'>Hours</span></div><div><span class='num'>" + duration.minutes()  + "</span><span class='label'>Mins</span></div><div><span class='num'>" + duration.seconds() + "</span><span class='label'>Secs</span></div>";
		}
	}, interval);

}());